.card { background-color: black; padding: 15px 10px; height: 470px; width: 350px; display: flex; flex-direction: column; flex-wrap: wrap; align-items: center; justify-content: center; text-align: center; border-radius: 10px; box-shadow: 5px 5px 6px gray; margin: 30px 15px; transition: all 0.5s ease; }
.card:hover {transform: scale(1.1);}
.name {color: skyblue}
.details {color: white; text-align: center;}
